import React, { Component } from "react";
import styles from './mystyle.module.scss';
import classNames from 'classnames/bind'
import history from "../../history";
import { connect } from "react-redux";
import { user } from '../../actions';
const cx = classNames.bind(styles)

class SignUp extends Component {
    componentDidMount() {
        if (JSON.stringify(this.props.user) !== '{}') {
            const r = window.confirm(' , ')
            if (r === true) {
                this.props.logOut()
            } else {
                history.push('/')
            }
        }
    }
    constructor(props) {
        super(props);
        this.state = {
            c_log: 1,
            username: '',
            password: '',
            password2: '',
            hasError: false
        };
    }
    static getDerivedStateFromError(error) {
        return { hasError: true }
    }
    ValidateEmail() {
        if (/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(this.state.username)) {
            return true
        } else {
            return false
        }
    }
    validatePwd() {
        if (this.state.password.length < 8 ) {
            return false
        } else {
            if (this.state.password === this.state.password2) {
                return true
            } else {
                return false
            }
            
        }
    }
    async handleSummit() {
        if (this.state.hasError) {
            window.alert('   .  .')
            await this.setState({username: '', password: '', hasError:false})
        } else {
            const validEmail = this.ValidateEmail()
            const validPwd = this.validatePwd()
            if ( ( validEmail === true ) & ( validPwd === true ) ) {
                await this.props.register(this.state.username, this.state.password)
                window.alert(' ')
                history.push('/')
            } else {
                window.alert(' ')
            }
        }
    }
    render() {
        return (
            <div className={cx('container')}>
                <div className={cx('category')}><p>Sign Up</p></div>

                <div className={cx('basic-box')}>
                    <p></p>
                    <input 
                        type="text" 
                        className={cx('input-box')} 
                        placeholder=" " 
                        onChange={(e) => this.setState({username: e.target.value})}></input>
                    <p></p>
                    <input 
                        type="password" 
                        className={cx('input-box')} 
                        placeholder="8   " 
                        onChange={(e) => this.setState({password: e.target.value})}></input>
                    <p> </p>
                    <input 
                        type="password" 
                        className={cx('input-box')} 
                        placeholder="  " 
                        onChange={(e) => this.setState({password2: e.target.value})}></input>
                    <div className={cx('border-button')} onClick={() => this.handleSummit()}>
                        <p></p>
                    </div>
                    <p>  <a onClick={() => //history.replace(history.length - 1, '/SignIn')
                                         history.push('/SignIn')
                    }></a></p>
                </div>
            </div>
        );
    }
}

const mapStatetoProps = state => {
    return {
        user: state.user.user
    };
};

const mapStateToDispatch = dispatch => {
    return {
        register: (id, pwd) => {dispatch(user.register(id, pwd))},
        logOut: () => {dispatch(user.logOut())}
    }
}

export default (connect(mapStatetoProps, mapStateToDispatch)(SignUp));