import React from "react";

import styles from './mystyle.module.scss';
import classNames from 'classnames/bind'
// import imgA from "../../assets/imgA.png";
import plus_b from "../../assets/plus_b.png";
import MyPetList from "../../components/MyPetList/MyPetList";
import MyInfo from "../../components/MyInfo/MyInfo";
import Modal from '@material-ui/core/Modal';
// import { user } from "../../actions"
const cx = classNames.bind(styles)

class MyPage extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            isSearching: false,
            a_name: '',
            a_type: '',
            a_species: '',
            a_kig: 0,
            a_year: 0,
            user: this.props.user
        }
        this.toggleAdd = this.toggleAdd.bind(this);
        this.addPetName = this.addPetName.bind(this);
        this.onSelect = this.onSelect.bind(this);
    }

    componentDidMount() {
    }
    async onSelect(e) {
        this.state.a_species = e;
    }
    toggleAdd(e) {
        this.setState({
            isSearching: e,
        })
    }
    addPetName(e) {
        this.setState({
            a_name: e,
        })
    }
    addMyPet() {

    }
    render() {

        const body = (
            <div className={cx('modal')}>
                <p>  </p>
                <input type="text" className={cx('input-box')} placeholder="  " onChange={this.addPetName}></input>
                <label> </label>
                <select name="speices" onChange={e => this.onSelect(e.target.value)}>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                </select>
                <p> </p>
                <input type="text" className={cx('input-box')} placeholder=" " onChange={this.addPetName}></input>
                <p> </p>
                <input type="text" className={cx('input-box')} placeholder=" " onChange={this.addPetName}></input>
                <div onClick={() => this.toggleAdd(false)}>
                    <p> </p>
                </div>
            </div>

        );


        return (
            <div>
                <div className={cx('row')}>
                    <div className={cx('small-col')}>
                    </div>
                    <div className={cx('spacer')}></div>
                </div>
                <div className={cx('category')}>
                    <p>  </p>
                </div>
                <div>
                    <MyInfo user = {this.state.user}/>
                    {/* <MyInfo user = {this.state.user}/> */}
                </div>
                <div className={cx('category')}>
                    <p>  </p>
                </div>
                <div>
                    <MyPetList />
                </div>
                <div align="center" onClick={() => this.toggleAdd(true)}>
                    <img src={plus_b} width="20" height="20" alt=""></img>
                </div>
                <div className={cx('category')}>
                    <p>  </p>
                </div>
                <div>
                    {/* <MyInfo /> */}
                </div>
                <div className={cx('category')}>
                    <p>  </p>
                </div>
                <div>
                    {/* <MyInfo /> */}
                </div>
                <Modal
                    open={this.state.isSearching}
                >
                    {body}
                </Modal>
            </div>
        );
    }
}



export default MyPage;
