        let list, reviewCards;
        if (true) {
            list = this.props.location.state.localRev;
            reviewCards = list.map(r => <HosReviewInfo hospitalData={r} key={`newCard${r.hcode}`}/>)
          } else {
            return (
              reviewCards = null
            )
          }
