import React, { Component } from "react";
import styles from './mystyle.module.scss';
import classNames from 'classnames/bind'
import { connect } from "react-redux";
import { user } from '../../actions';
import history from "../../history";
const cx = classNames.bind(styles)

class SignInPage extends Component {
    componentDidMount() {
        if (JSON.stringify(this.props.user) !== '{}') {
            const r = window.confirm(' , ')
            if (r === true) {
                this.props.logOut()
            } else {
                history.push('/')
            }
        }
    }
    constructor(props) {
        super(props);
        this.state = {
            username: '',
            password: '',
            hasError: false,
        };
    }
    static getDerivedStateFromError(error) {
        return { hasError: true }
    }

    async handleSummit() {
        if (this.state.hasError) {
            window.alert('   .  .')
            await this.setState({username: '', password: '', hasError:false})
        } else {
            await this.props.signIn(this.state.username, this.state.password)
            window.alert('')
            history.push('/')
        }
    }
    render() {
        // console.log(" : ", this.props.user)
        return (
            <div className={cx('container')}>
                <div className={cx('category')}><p>Sign In</p></div>
                <div className={cx('basic-box')}>
                    <p></p>
                    <input
                        type="text"
                        className={cx('input-box')}
                        placeholder=" "
                        onChange={(e) => this.setState({ username: e.target.value })}></input>
                    <p></p>
                    <input
                        type="password"
                        className={cx('input-box')}
                        placeholder=" "
                        onChange={(e) => this.setState({ password: e.target.value })}></input>
                    <div className={cx('border-button')} onClick={() => this.handleSummit()}>
                        <p></p>
                    </div>
                    <p>   <a onClick={() => history.push('/SignUp')}></a></p>
                </div>
            </div>
        );
    }
}

const mapStatetoProps = state => {
    return {
        user: state.user.user
    }
};

const mapDispatchToProps = dispatch => ({
    signIn: (uid, pwd) => dispatch(user.signIn(uid, pwd)),
    logOut: () => dispatch(user.logOut())
})

export default (connect(mapStatetoProps, mapDispatchToProps)(SignInPage));